//Burgermenu til mobile
.burgerbar {
  position: fixed;
  top: 0;
  right: -250px;
  background: #fafafa;
  width: 230px;
  height: 100%;
  transition: .4s;
  @extend %shadow;
  z-index: 10000
}

.active {
  right: 0;
}

ul {
  margin: 0;
  padding: 20px 0;
}

ul li {
  list-style: none;
}

ul li a {
  padding: 10px 20px;
  color: #000000;
  font-weight: bold;
  display: block;
  text-decoration: none;
  border-bottom: 1px solid rgba(0, 0, 0, .2);
}

// her laver vi den fysise burgerknap
.burger-btn {
  position: fixed;
  top: 10px;
  right: 10px;
  width: 50px;
  height: 50px;
  box-sizing: border-box;
  border: none;
  outline: none;
  background: transparent;
  z-index: 10001
}

.burger-btn span {
  display: block;
  width: 35px;
  height: 3px;
  background: #252525;
  position: absolute;
  transition: .4s;
  border-radius: 3px;
}

// Her koder vi så burgeren bliver til et kryds
// til at lukke for menuen igen.
// dette gør vi ved at animere vores Span elementer
// vi har 4 streger i alt. 2 ovenpå hinanden
// de to ydre strejer forsvinder med opacity 0 transition
// krydset kommer af det 2 streger i midten roterer.
.burger-btn span:nth-child(1) {
  top: 10px;
  -webkit-transform-origin: left center;
  -moz-transform-origin: left center;
  -o-transform-origin: left center;
  transform-origin: left center;
}

.burger-btn span:nth-child(2) {
  top: 20px;

}

.burger-btn span:nth-child(3) {
  top: 20px;

}

.burger-btn span:nth-child(4) {
  top: 30px;
  -webkit-transform-origin: right center;
  -moz-transform-origin: right center;
  -o-transform-origin: right center;
  transform-origin: right center;
}

.burger-btn.open span:nth-child(1) {
  opacity: 0;
}

.burger-btn.open span:nth-child(2) {

  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}

.burger-btn.open span:nth-child(3) {

  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.burger-btn.open span:nth-child(4) {
  opacity: 0;
}

